"use client";

import React, { useEffect, useRef } from "react";

const HeroSection = () => {
  const marqueeRef = useRef<HTMLDivElement>(null);
  const requestRef = useRef<number>(0);
  const position = useRef(0);
  const direction = useRef(1); // 1 = left→right, -1 = right→left
  const currentSpeed = useRef(2); // px per frame
  const lastScrollY = useRef(0); // track last scroll position

  const animate = () => {
    if (marqueeRef.current) {
      const width = marqueeRef.current.scrollWidth / 2;
      position.current += currentSpeed.current * direction.current;

      if (position.current < -width) position.current += width;
      if (position.current > 0) position.current -= width;

      marqueeRef.current.style.transform = `translateX(${position.current}px)`;
    }
    requestRef.current = requestAnimationFrame(animate);
  };

  const handleScroll = () => {
    const currentScrollY = window.scrollY;

    if (currentScrollY > lastScrollY.current) {
      // scrolling down → right to left
      direction.current = -1;
    } else if (currentScrollY < lastScrollY.current) {
      // scrolling up → left to right
      direction.current = 1;
    }

    lastScrollY.current = currentScrollY;
  };

  useEffect(() => {
    requestRef.current = requestAnimationFrame(animate);
    window.addEventListener("scroll", handleScroll);
    return () => {
      cancelAnimationFrame(requestRef.current);
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <section className="h-screen w-full flex items-end justify-start bg-[#9a9a98] text-white relative overflow-hidden">
      {/* Centered background image */}
      <img
        src="images/photo.png"
        alt="Background"
        className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 pointer-events-none h-full w-auto"
      />

      {/* Marquee text */}
      <div
        ref={marqueeRef}
        className="absolute bottom-10 flex whitespace-nowrap text-[16vw] font-normal  leading-none tracking-tight"
      >
        <span className="mr-20"> — ANGHELO — DEARROZ</span>
        <span className="mr-20"> — ANGHELO — DEARROZ</span>
      </div>
    </section>
  );
};

export default HeroSection;
